import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import firebase from 'firebase/compat/app';
import { ADMIN_UIDS } from 'constants/auth';

const useAuth = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const user =
    JSON.parse(localStorage.getItem('user')) || firebase.auth().currentUser;
  const isLogin = Boolean(user) && ADMIN_UIDS.includes(user.uid);
  const isAtLoginPage = location.pathname === '/login';

  React.useEffect(() => {
    if (!isLogin && !isAtLoginPage) {
      navigate('/login');
    }
  }, [isLogin, isAtLoginPage, navigate]);

  return { user, isLogin };
};

export default useAuth;
